export default {
    data() {
        return {
            todayStep: 0,
            pic_list: [],
            poster: '',
            pic: [],
            page: 0,
            currentItem: '',
            left: false,
            right: true,
        }
    },
    methods: {
        screen() {
            let img = this.poster
            uni.previewImage({
                urls: [img]
            })
        },
        chooseImg(id) {
            let that = this;
            let length = Math.floor(that.pic_list.length / 4 -0.0001);
            if(id == 1) {
                if (that.left == false ) {
                    return
                }
                that.page--;
                that.right = true;
                if (that.page == 0) {
                    that.left = false;
                }
                that.pic = that.pic_list.slice(that.page * 4, that.page * 4 + 4)
            } else if (id == 2){
                if (that.right == false) {
                    return
                }
                that.page++;
                that.left = true;
                if (that.page == length) {
                    that.right = false;
                }
                that.pic = that.pic_list.slice(that.page * 4, that.page * 4 + 4)
            }
        },
        tagChoose(id) {
            this.currentItem = id;
            this.getPoster();
        },
        saveImage: function () {
            let that = this;

            uni.showLoading({
                mask: true,
                text: '保存中...'
            });
            uni.authorize({
                scope: 'scope.writePhotosAlbum',
                success(res) {
                    if (res.errMsg == "authorize:ok") {
                        uni.getImageInfo({
                            src: that.poster,
                            success(res) {
                                uni.hideLoading();
                                uni.saveImageToPhotosAlbum({
                                    filePath: res.path,
                                    success(res) {
                                        uni.showToast({
                                            title: '保存成功！',
                                            icon: 'success',
                                            duration: 2000
                                        });
                                    },
                                    fail(res) {
                                        uni.showToast({
                                            title: '保存失败！',
                                            icon: 'none',
                                            duration: 1000
                                        });
                                    }
                                })
                            },
                            fail(res) {
                                uni.hideLoading();
                                uni.showToast({
                                    title: '保存失败！',
                                    icon: 'none',
                                    duration: 1000
                                });
                            }
                        })
                    }
                },
                fail(res) {
                    uni.hideLoading();
                    uni.showModal({
                        title: '提示',
                        content: '保存失败，需要授权保存到相册权限',
                        showCancel: false,
                        confirmText: '打开授权',
                        success(e) {
                            if (e.confirm) {
                                uni.openSetting({});
                            }
                        }
                    });
                }
            })



























        },
        getPoster() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '生成中...'
            });
            that.$request({
                url: that.$api.step.poster,
                data: {
                    pic_id: that.currentItem,
                    num: that.todayStep
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.poster = response.data.pic_url;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '步数加载中...'
            });
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.pic_list = response.data.qrcode_pic;
                    that.pic = response.data.qrcode_pic;
                    that.qrcode_title = response.data.qrcode_title;
                    that.currentItem = response.data.qrcode_pic[0].id;
                    if(response.data.qrcode_pic.length > 4) {
                        that.pic = response.data.qrcode_pic.slice(0, 4);
                    }else {
                        that.left = false;
                        that.right = false;
                    }
                    that.getPoster();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if (options.step > -1) {
            that.todayStep = options.step
        }
        that.getList();
    }
}
